"use client";

import { useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Mic,
  FileText,
  BarChart,
  Timer as TimerIcon,
  Clock as ClockIcon,
  Clock3,
} from "lucide-react";
import { UploadDialog } from "./components/UploadDialog";
import { SearchBar } from "./components/SearchBar";

export default function DashboardPage() {
  const [uploadDialogOpen, setUploadDialogOpen] = useState(false);

  const DashboardContent = () => (
    <div className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="pt-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-purple-600 font-medium">
                  Totalt antall opptak
                </p>
                <h3 className="text-2xl font-semibold">0</h3>
              </div>
              <div className="p-2 rounded-lg">
                <BarChart className="h-6 w-6 text-violet-600" />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="pt-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-purple-600 font-medium">
                  Timer totalt
                </p>
                <h3 className="text-2xl font-semibold">0t 0m</h3>
              </div>
              <div className="p-2 rounded-lg">
                <Clock3 className="h-6 w-6 text-violet-600" />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="pt-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-purple-600 font-medium">Tid spart</p>
                <h3 className="text-2xl font-semibold">0m</h3>
              </div>
              <div className="p-2 rounded-lg">
                <TimerIcon className="h-6 w-6 text-violet-600" />
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="pt-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm text-purple-600 font-medium">
                  Snitt varighet
                </p>
                <h3 className="text-2xl font-semibold">0s</h3>
              </div>
              <div className="p-2 rounded-lg">
                <ClockIcon className="h-6 w-6 text-violet-600" />
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <Card className="cursor-pointer group">
          <CardContent className="pt-4">
            <div className="flex items-center gap-4">
              <div className="p-3 rounded-lg bg-violet-600 group-hover:bg-violet-700 transition-colors">
                <Mic className="h-6 w-6 text-white" />
              </div>
              <div>
                <h3 className="text-lg font-semibold">Start opptak</h3>
                <p className="text-sm text-muted-foreground">
                  Begynn å ta opp et møte nå
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card
          className="cursor-pointer group"
          onClick={() => setUploadDialogOpen(true)}
        >
          <CardContent className="pt-4">
            <div className="flex items-center gap-4">
              <div className="p-3 rounded-lg bg-violet-100 group-hover:bg-violet-200 transition-colors">
                <FileText className="h-6 w-6 text-violet-600" />
              </div>
              <div>
                <h3 className="text-lg font-semibold">Last opp opptak</h3>
                <p className="text-sm text-muted-foreground">
                  Last opp eksisterende lydopptak
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      <div>
        <SearchBar />
      </div>

      <div className="bg-white rounded-lg p-4 shadow-sm border min-h-[calc(100vh-36rem)]">
        <div className="text-center text-gray-500 py-8">
          Ingen opptak funnet
        </div>
      </div>
    </div>
  );

  return (
    <div className="w-full h-full p-4">
      <div className="relative flex items-center mb-6">
        <h1 className="text-2xl font-semibold pl-10 lg:pl-0">Mine opptak</h1>
      </div>

      <Tabs defaultValue="totalt" className="space-y-6">
        <TabsList className="bg-transparent border-b border-gray-200 w-full flex justify-start gap-8 pb-0 overflow-x-auto">
          <TabsTrigger
            value="totalt"
            className="data-[state=active]:border-violet-600 data-[state=active]:bg-transparent border-b-2 border-transparent pb-2 rounded-none"
          >
            Totalt
          </TabsTrigger>
          <TabsTrigger
            value="idag"
            className="data-[state=active]:border-violet-600 data-[state=active]:bg-transparent border-b-2 border-transparent pb-2 rounded-none"
          >
            I dag
          </TabsTrigger>
          <TabsTrigger
            value="denneuken"
            className="data-[state=active]:border-violet-600 data-[state=active]:bg-transparent border-b-2 border-transparent pb-2 rounded-none"
          >
            Denne uken
          </TabsTrigger>
          <TabsTrigger
            value="dennemaneden"
            className="data-[state=active]:border-violet-600 data-[state=active]:bg-transparent border-b-2 border-transparent pb-2 rounded-none"
          >
            Denne måneden
          </TabsTrigger>
        </TabsList>

        <TabsContent value="totalt">
          <DashboardContent />
        </TabsContent>

        <TabsContent value="idag">
          <DashboardContent />
        </TabsContent>

        <TabsContent value="denneuken">
          <DashboardContent />
        </TabsContent>

        <TabsContent value="dennemaneden">
          <DashboardContent />
        </TabsContent>
      </Tabs>

      <UploadDialog
        open={uploadDialogOpen}
        onOpenChange={setUploadDialogOpen}
      />
    </div>
  );
}
